@keyframes kill {
    0% {
        background-color: rgba(0, 0, 0, 0.8);
    }
    2% {
        background-color: rgba(255, 255, 255, 1);
    }
   4% {
        background-color: rgba(255, 255, 255, 1);
    }
    6% {
        background-color: rgba(0, 0, 0, 1);
    }
    8% {
        background-color: rgba(255, 255, 255, 1);
    }
    10% {
        background-color: rgba(255, 255, 255, 1);
    }
    12% {
        background-color: rgba(0, 0, 0, 1);
    }
    14% {
        background-color: rgba(255, 255, 255, 1);
    }
    16% {
        background-color: rgba(255, 255, 255, 1);
    }
    18% {
        background-color: rgba(0, 0, 0, 1);
    }
    20% {
        background-color: rgba(0, 0, 0, 1);
    }
    22% {
        background-color: rgba(0, 0, 0, 0.5);
    }
    75% {
        background-color: rgba(255, 255, 255, 1);
    }
    100% {
        background-color: transparent;
    }
}
