@keyframes loading {
    0% {
        filter: invert(21%) sepia(99%) saturate(5716%) hue-rotate(356deg)
            brightness(97%) contrast(121%);
    }
    50% {
        filter: invert(60%) sepia(54%) saturate(2127%) hue-rotate(153deg)
            brightness(102%) contrast(105%);
        transform: rotateY(180deg);
    }
    100% {
        filter: invert(21%) sepia(99%) saturate(5716%) hue-rotate(356deg)
            brightness(97%) contrast(121%);
        transform: rotateY(360deg);
    }
}
